<template>
  <div class='main'>
    <div class="box">
      <div class="tile">自荐理由</div>
      <van-form @submit="onSubmit" :border="false" class="form" validate-trigger="onChange">
        <van-field v-model="liyou" maxlength="300" show-word-limit type="textarea" :autosize="autosize" name="liyou"
          label="" placeholder="自荐理由（必填）" :rules="[{ required: true }]" />
        <div class="btn">
          <van-button block type="info" native-type="submit">提交</van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>
<script>
import { taskBidder } from '@/api/request'
export default {
  data () {
    return {
      liyou: '',
      autosize: {
        minHeight: 50
      }
    }
  },
  methods: {
    async onSubmit (values) {
      let res = await taskBidder({
        task_id: +this.$route.query.task_id,
        reason: values.liyou
      })
      this.$router.go(-1)
    },
  }
}
</script>
<style lang='less' scoped>
.main {
  padding: 5px 10px;

  .box {
    background: #FFFFFF;
    border-radius: 10px;
    padding: 25px 10px 25px 10px;

    .tile {
      font-size: 14px;
      line-height: 1;
      font-weight: 400;
      color: #333333;
    }

    .form {
      margin-top: 15px;

      :deep(.van-cell) {
        padding: 0;

        textarea {
          background: #F6F6F6;
          border-radius: 3px;
          padding: 12px 13px;
        }

        .van-field__word-limit {
          font-size: 14px;
          font-weight: 400;
          color: #B1B5C5;
          margin-top: 10px;
        }
      }

      .van-cell::after {
        border: none;
      }
    }

    .btn {
      position: fixed;
      left: 10px;
      right: 10px;
      bottom: 60px;
    }
  }
}
</style>